/* Add this CSS to your existing styles or create a new CSS file (CartPageStyles.css) */

.cart-page {
  margin: 20px;
}

.bg-light {
  background-color: #f8f9fa !important;
}

.cart-page .card {
  width: 100%;
  border: 1px solid #dee2e6;
  object-fit: cover;
  border-radius: 8px;
  margin-bottom: 20px;
  overflow: hidden;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease-in-out;
}

.card img {
  width: 100%;
  height: auto;
  border-top-left-radius: 8px;
  border-top-right-radius: 8px;
}

.card:hover {
  transform: scale(1.05);
}

.cart-remove-btn button {
  width: 100%;
}

.cart-summary {
  background-color: #f8f9fa;
  border-radius: 8px;
  padding: 20px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.btn-outline-warning,
.btn-outline-warning:hover {
